/* SCSS HEX */
$color100: #0b3954ff;
$color90: #708b88ff;
$color80: #8aa4a9ff;
$color70: #a5becaff;
$color60: #bfd7eaff;
$color50: #cfe1efff;
$color40: #dfebf4ff;
$color00: #fefffeff;


/* SCSS HSL */
$color100: hsla(202, 77%, 19%, 1);
$color90: hsla(173, 11%, 49%, 1);
$color80: hsla(190, 15%, 60%, 1);
$color70: hsla(199, 26%, 72%, 1);
$color60: hsla(207, 51%, 83%, 1);
$color50: hsla(206, 50%, 87%, 1);
$color40: hsla(206, 49%, 92%, 1);
$color00: hsla(120, 100%, 100%, 1);

/* SCSS RGB */
$color100: rgba(11, 57, 84, 1);
$color90: rgba(112, 139, 136, 1);
$color80: rgba(138, 164, 169, 1);
$color70: rgba(165, 190, 202, 1);
$color60: rgba(191, 215, 234, 1);
$color50: rgba(207, 225, 239, 1);
$color40: rgba(223, 235, 244, 1);
$color00: rgba(254, 255, 254, 1);

/* SCSS Gradient */
$gradient-top: linear-gradient(0deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-right: linear-gradient(90deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-bottom: linear-gradient(180deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-left: linear-gradient(270deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-top-right: linear-gradient(45deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-bottom-right: linear-gradient(135deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-top-left: linear-gradient(225deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-bottom-left: linear-gradient(315deg, #0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);
$gradient-radial: radial-gradient(#0b3954ff, #708b88ff, #8aa4a9ff, #a5becaff, #bfd7eaff, #cfe1efff, #dfebf4ff, #fefffeff);